* {
  margin: 0;
  padding: 0; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 62.5%; }

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  line-height: 1.6;
  background-color: #420c34; }

/* 
////////////////PRE SET/////////////////////////// */
.wraper-ceo {
  height: 100%;
  width: 100%;
  margin-top: 30px; }

.wraper-tabla {
  width: 100%;
  height: 20%;
  padding-top: 100%;
  /* 1:1 Aspect Ratio */
  position: relative;
  /* If you want text inside of it */ }

.container-tabla {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0; }

.coin-neutral {
  color: #420c34;
  float: left;
  background-color: #420c34;
  border: 1px solid #f2f2f2;
  height: 14.2%;
  width: 14.2%;
  font-size: 0.1rem; }

.coin-yellow {
  color: transparent;
  background-color: #dada14;
  border-radius: 100%;
  height: 100%;
  width: 100%;
  font-size: 0.1rem;
  -webkit-animation-name: coin;
          animation-name: coin;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

.coin-red {
  color: transparent;
  background-color: #E30425;
  border-radius: 100%;
  height: 100%;
  width: 100%;
  font-size: 0.1rem;
  -webkit-animation-name: coin;
          animation-name: coin;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

.pobeda {
  position: relative;
  display: inline-block;
  color: #f2f2f2;
  background-color: #420c34;
  border: 1px solid #f2f2f2;
  height: 80px;
  width: 100%;
  font-size: 120%; }

.pobednik {
  position: absolute;
  top: 25%;
  left: 50%;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 50%; }

.naReduJe {
  position: absolute;
  top: 30%;
  left: 2%;
  bottom: 0;
  right: 0;
  width: 20%;
  height: 20%;
  font-size: 1.2rem; }

.coinY {
  position: absolute;
  top: 20%;
  left: 30%;
  bottom: 0;
  right: 0;
  background-color: #dada14;
  height: 40px;
  width: 40px;
  border-radius: 100%; }

.coinR {
  position: absolute;
  top: 20%;
  left: 30%;
  bottom: 0;
  right: 0;
  background-color: #E30425;
  height: 40px;
  width: 40px;
  border-radius: 100%; }

@-webkit-keyframes coin {
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1); }
  100% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8); } }

@keyframes coin {
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1); }
  100% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8); } }

.semafor {
  background-color: #420c34;
  color: #f2f2f2;
  height: 8vh;
  font-size: 1rem;
  margin-bottom: 7px; }

button {
  border: 1px solid #f2f2f2; }

.button-newGame {
