.button-winR {
  float: left;
  color: #f2f2f2;
  background-color: #E30425;
  font-size: 200%;
  font-weight: 500;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  width: 20%;
  height: 100%;
  padding: 0.5%; }

.playerY {
  float: left;
  border: 1px solid #f2f2f2;
  text-align: center;
  padding-top: 23px;
  font-size: 120%;
  width: 20%;
  height: 100%; }

.playerR {
  float: left;
  border: 1px solid #f2f2f2;
  text-align: center;
  padding-top: 23px;
  font-size: 120%;
  width: 20%;
  height: 100%; }

.animation-red {
  background-color: #E30425;
